import React, { Component } from 'react';
import Tooltip from 'rc-tooltip'; //tooltips hints
import InputMask from 'react-input-mask'; //mask for input fields
import $ from 'jquery'; 


import arrow from '../choose/arrow.svg';
import cardlogo from './visaMC.png'
import cvvexample from '../paymentform/cvv.png';
import './paymentForm.css';
import 'rc-tooltip/assets/bootstrap.css';

class PaymentForm extends Component {

  constructor(props){
    super();

    this.state = {
      email: '', 
      ccno: '', // Credit card number
      cvv: '', // Credit card  cvv
      expmonth: '01',  // Credit card  expiration month
      expyear: '18', // Credit card  expiration year
      isloading: false,  // sending ajax
      emailtooltipvisible: false, // hints
      ccnotooltipvisible: false,
      cvvtooltipvisible: false,
      responce: {  
        crm: ''
      } 

    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.emailChange = this.emailChange.bind(this);
    this.ccnoChange  = this.ccnoChange.bind(this);
    this.cvvChange  = this.cvvChange.bind(this);
    this.handleChange = this.handleChange.bind(this);

    this.validateEmail  = this.validateEmail.bind(this);
    this.validateCVV  = this.validateCVV.bind(this);
    this.validateCCN  = this.validateCCN.bind(this);
  }

//Change input field event
  ccnoChange = (event) => { //set ccno field
    var card = event.target.value;
    var newState = {
      ccno: card,
      ccnotooltipvisible: false,
    };
    if (/^3[47]/.test(card)) {
      newState.mask = '9999-999999-99999';
    }
    this.setState(newState);
  }

  cvvChange = (event) => { //set ccno field
    var cvv = event.target.value;
    var newState = {
      cvv: cvv,
      cvvtooltipvisible: false,
    };
    this.setState(newState);
  }

  emailChange(e){                //pass field value to state
    this.setState({
      email: e.target.value,
      emailtooltipvisible: false,
    });    
  }

  handleChange(e){                //month, year fields
    this.setState({
      [e.target.name]: e.target.value
    });    
  }

//Validation input fields

  validateEmail = () => {         //validate email
    var reg = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return reg.test(String(this.state.email).toLowerCase());
  }

  validateCVV = () => {         //validate cvv
    let newcvv = +this.state.cvv;
    if ( isNaN(newcvv) === false && newcvv > 0 && newcvv < 1000 ){
      return true
    } else {
        return false
    } 
  }

  validateCCN = () => {         //validate ccn
    var cardstr = this.state.ccno;
    var str = cardstr.replace(/[-_]/g, '');
    if ( str.length === 16 ){
      return true
    } else {
        return false
    } 
    
  }

//Submit From

  handleSubmit(event) {           

    event.preventDefault();

    this.setState({    //disable all tooltips
      emailtooltipvisible: false,
      ccnnotooltipvisible: false,
      cvvtooltipvisible: false,
      responce: '',
    });

    if (this.validateEmail() === false) {    //if email not valid show hints
      this.setState({
        emailtooltipvisible: true,
        emailtext: 'Not valid email',
      });
      return
    }

    if (this.validateCCN() === false) {  //if ccn not valid show hints
      this.setState({
        ccnnotooltipvisible: true,
        ccnotext: 'Not valid card number',
      });
      return
    }

    if (this.validateCVV() === false) { //if cvv not valid show hints
      this.setState({
        cvvtooltipvisible: true,
        cvvtext: 'Not valid CVV code',
      });
      return
    }

//AJAX request 
  $.ajax({
        type: 'POST',
        url: 'https://usalws.org//crm/configs/import/etc/mc/index.php',
        data: {
          salt: 'DF0F31AA2988C0ABD3CC34E249477438',
          email: this.state.email,
          ccno: +this.state.ccno.replace(/[-_]/g, ''),    //delete all - and _ from card number
          cvv:  +this.state.cvv,
          expmonth: +this.state.expmonth,
          expyear: +this.state.expyear,
          istesting: true
        },
        beforeSend: (xhr)=>{
          this.setState({isloading: true});   //show preloader on button
      },complete: (xhr)=>{
        this.setState({isloading: false})  //hide preloader
  },
        success: (resultData) => { 
          this.setState({
            responce: resultData,
          });
          //Show messages
          if (resultData.psp === 'Defective card') { this.setState({ccnnotooltipvisible: true,
            ccnotext: 'Defective card',}) }
          if (resultData.crm === 'Already registered with another campaign') { this.setState({emailtooltipvisible: true,
            emailtext: 'Already registered with another campaign',}) }
          // Add redirrect to another page when we receive  
      }
  });


  }

  render() {
    return (

      <section>
        <div className="select">
            <img src={arrow} alt="arrow"/>
            <p className="select-txt">Payment Information</p>      
        </div>
        <div className="card-select">
          <label htmlFor="payment-method">
            <input type="radio" name="payment-method" value="credit-card"></input>
            <span>Credit Card</span>
            <img src={cardlogo} alt="card"/>
            </label>
            <div className="payment-desc">
            Pay securely with a credit card
            </div>
          </div>

          <p className="form-heading">Fill your credit/debit card details</p>

          <form action="" id="payment" onSubmit={this.handleSubmit}>

              <p>USA visa Expert Assessment program</p>

              <div className="single-field">
                  <label htmlFor="email">Email<i>*</i> </label>
                  <Tooltip
                          visible={this.state.emailtooltipvisible}
                          defaultVisible={false}
                          animation="zoom"
                          id="emailhint"
                          trigger={[]}
                          overlayStyle={{ zIndex: 1000 }}
                          overlay={<span>{this.state.emailtext}</span>}
                        >
                  <input aria-describedby="emailhint" id="email" name="email" type="email" onChange={this.emailChange} placeholder="Enter email" required />      
                  </Tooltip>
              </div>

              <div className="single-field">
                  <label htmlFor="amount" className="amount">Total Amount</label>
                  <span className="amount">$75</span>
              </div>

              <div className="single-field">
                  <label htmlFor="card-number">Card Number<i>*</i></label>
                  <Tooltip
                          visible={this.state.ccnnotooltipvisible}
                          animation="zoom"
                          id="ccnohint"
                          trigger={[]}
                          overlayStyle={{ zIndex: 1000 }}
                          overlay={<span>{this.state.ccnotext}</span>}
                        > 
                  <InputMask {...this.state} mask="9999-9999-9999-9999" aria-describedby="ccnolhint" onChange={this.ccnoChange} maskChar="_" placeholder="Enter card number" id="card-number" required/> 
                  </Tooltip>
              </div>
            
              <div className="single-field">
                  <label htmlFor="expmonth">Expiration<i>*</i></label>
                  <select value={this.state.expmonth} onChange={this.handleChange} name="expmonth" required>
                    <option value="01">1</option>
                    <option value="02">2</option>
                    <option value="03">3</option>
                    <option value="04">4</option>
                    <option value="05">5</option>
                    <option value="06">6</option>
                    <option value="07">7</option>
                    <option value="08">8</option>
                    <option value="09">9</option>
                    <option value="10">10</option>
                    <option value="11">11</option>
                    <option value="12">12</option>
                  </select>
                  <span> / </span>
                  <select value={this.state.expyear} onChange={this.handleChange} name="expyear" required>
                    <option value="18">2018</option>
                    <option value="19">2019</option>
                    <option value="20">2020</option>
                    <option value="21">2021</option>
                    <option value="22">2022</option>
                    <option value="23">2023</option>
                    <option value="24">2024</option>
                    <option value="25">2025</option>
                    <option value="26">2026</option>
                    <option value="27">2027</option>
                    <option value="28">2028</option>
                    <option value="29">2029</option>
                    <option value="30">2030</option>
                  </select>
              </div>

              <div className="single-field">
              <Tooltip  
                          defaultVisible={false}
                          animation="zoom"
                          placement="top"
                          id="card-hint"
                          trigger={['click']}
                          overlayStyle={{ zIndex: 1000 }}
                          overlay={<img src={cvvexample} alt="CVV code info"/>    }
                        >
                  <label htmlFor="cvv" id="card-hint">Security Code<i>*</i></label>
                  </Tooltip>
                  <Tooltip
                          visible={this.state.cvvtooltipvisible}
                          defaultVisible={false}
                          animation="zoom"
                          id="cvvhint"
                          trigger={[]}
                          align={{
                            offset: [50, 0],
                          }}
                          overlayStyle={{ zIndex: 1000 }}
                          overlay={<span>{this.state.cvvtext}</span>}
                        >
                  <InputMask aria-describedby="cvvhint" mask="999" placeholder="CVV" maskChar="X" 
                  onChange={this.cvvChange}  id="cvv" 
                  name='cvv'/>
                  </Tooltip>
              </div>

              <div className="single-field">
              <label htmlFor=""></label>           
              <div className="spinner">
                <div className={"bounce1 " + (this.state.isloading ? 'show' : 'hide')}></div>
                <div className={"bounce2 " + (this.state.isloading ? 'show' : 'hide')}></div>
                <div className={"bounce3 " + (this.state.isloading ? 'show' : 'hide')}></div>
                <p className={"bg-box " + (this.state.isloading ? 'show' : 'hide')}></p>
                <input  type="submit" value="Pay Now"/>
              </div>
              <p className="responce-txt">{this.state.responce.crm ? this.state.responce.crm : this.state.responce.psp}</p>
              </div>
          </form>

      </section>
    );
  }
}

export default PaymentForm;
